.App {
  box-sizing: border-box;
  font-family: "Rubik", sans-serif;
  text-align: center;
  color: #393c41;
  margin: 0;
  padding: 0;
}
body {
  width: 50%;
  overflow: inherit;
}
@keyframes animateDown {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }

  40% {
    transform: translateY(5px);
  }
  60% {
    transform: translateY(3px);
  }
}
